import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";

const routes = [
	{
		path: "/",
		name: "Home",
		component: Home,
		meta: {
			title: "Home",
		},
	},
	{
		path: "/about",
		name: "About",
		component: () =>
			import("../views/About.vue"),
		meta: {
			title: "About",
		},
	},
	{
		path: "/patients",
		name: "Patienten",
		component: () => import("../views/Patient/Patients.vue"),
		meta: {
			title: "Patienten",
		},
	},
	{
		path: "/login",
		name: "Login",
		component: () => import("../views/Login.vue"),
		meta: {
			title: "Login",
		},
	},
	{
		path: "/profile",
		name: "Profile",
		component: () => import("../views/Profile.vue"),
		meta: {
			title: "Profile",
		},
	},
	{
		path: "/patient/:id",
		name: "Patient",
		component: () => import("../views/Patient/Patient.vue"),
		meta: {
			title: "Patient",
		},
	},
	{
		path: "/patient/:id/createPrescription",
		name: "CreatePrescription",
		component: () => import("../views/Prescription/CreatePrescription.vue"),
		meta: {
			title: "Neues Rezept",
		},
	},
	{
		path: "/patient/:id/createPrescriptionFromDischarge/:dischargeId",
		name: "CreatePrescriptionFromDischarge",
		component: () => import("../views/Prescription/CreatePrescription.vue"),
		meta: {
			title: "Neues Rezept",
		},
	},
	{
		path: "/patient/:id/createDischargeSummary",
		name: "CreateDischargeSummary",
		component: () => import("../views/DischargeSummary/CreateDischargeSummary.vue"),
		meta: {
			title: "Neues Entlassungsbrief",
		},
	},
	{
		path: "/patient/:patientId/discharge-summary/:dischargeId",
		name: "ViewDischargeSummary",
		component: () => import("../views/DischargeSummary/ViewDischargeSummary.vue"),
		meta: {
			title: "Entlassungsbrief",
		},
	},
	{
		path: "/patient/:patientId/prescription/:prescriptionId",
		name: "ViewPrescription",
		component: () => import("../views/Prescription/ViewPrescription.vue"),
		meta: {
			title: "Rezept",
		},
	},
	{
		path: "/open-discharges",
		name: "OpenDischarges",
		component: () => import("../views/DischargeSummary/OpenDischarges.vue"),
		meta: {
			title: "Offene Aufgaben",
		},
	},
];

const router = createRouter({
	history: createWebHistory(process.env.BASE_URL),
	routes,
});

router.beforeEach((toRoute, fromRoute, next) => {
	window.document.title =
		(toRoute.meta && toRoute.meta.title ? toRoute.meta.title : "Home") +
		" | Linked Care";

	next();
});

export default router;
