import * as Vue from "vue";
import Vuex from "vuex";
import App from "./App.vue";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap";
import axios from "axios";
import VueAxios from "vue-axios";
import router from "./router";
import Paginate from "vuejs-paginate-next";
import VueFeather from "vue-feather";

let app = Vue.createApp(App);

app.config.globalProperties.$baseApiUrl = "http://172.29.16.61/";
app.config.globalProperties.$apiUrl = `${app.config.globalProperties.$baseApiUrl}/fhir`;

app.config.globalProperties.$logout = () => {
	localStorage.removeItem("loggedIn");
	localStorage.removeItem("user");
	console.log("[INFO] User logout");
	window.location.href = "/";
};

app.config.globalProperties.$getUserEmail = () => {
	if (localStorage.getItem("user") != null) {
		return JSON.parse(localStorage.getItem("user")).email;
	}
	return "";
};

app.config.globalProperties.$getUserId = () => {
	if (localStorage.getItem("user") != null) {
		return JSON.parse(localStorage.getItem("user")).id;
	}
	return "";
};

app.config.globalProperties.$getUserRole = () => {
	if (localStorage.getItem("user") != null) {
		return JSON.parse(localStorage.getItem("user")).role;
	}
	return 0;
};

app.config.globalProperties.$AuthorizedRole = (listOfAuthorisedRoles) => {
	let userRole = app.config.globalProperties.$getUserRole();
	for (let role of listOfAuthorisedRoles) {
		if (role === userRole) {
			return true;
		}
	}
	return false;
};

app.config.globalProperties.$redirectNotAuthorizedToLogin = (
	listOfAuthorisedRoles
) => {
	if (app.config.globalProperties.$AuthorizedRole(listOfAuthorisedRoles)) {
		console.log("[INFO] User is authorized to open this page");
		return true;
	}
	console.log("[INFO] User is not authorized to open this page");
	router.push("/login");
	return false;
};

app.config.globalProperties.$getUserRoleString = () => {
	let userRole = 0;
	if (localStorage.getItem("user") != null) {
		userRole = JSON.parse(localStorage.getItem("user")).role;
	}
	switch (userRole) {
		case 1:
			return "Krankenhaus";
		case 2:
			return "Arzt";
		case 3:
			return "Patient";
		case 4:
			return "Apotheke";
		case 0:
			return "undefined";
	}
};

app.config.globalProperties.$isLoggedIn = () => {
	if (localStorage.getItem("loggedIn") === null) {
		return false;
	}
	return true;
};

app.component(VueFeather.name, VueFeather);

app.use(router, VueAxios, axios, Vuex, Paginate).mount("#app");
