<template>
    <div class="home">
        <p v-if="$isLoggedIn()">Hello your are logged in as {{ $getUserEmail() }}</p>
    </div>
</template>

<script>
export default {
    name: 'Home',
    beforeCreate() {
        if (!this.$isLoggedIn()) {
            this.$router.push("/login")
        }
        switch (this.$getUserRole()) {
            // Krankenhaus
            case 1:
                this.$router.push("/patients");
                break;
            // Arzt
            case 2:
                this.$router.push("/open-discharges");
                break;
            // Patient
            case 3:
                this.$router.push("/patient/" + this.$getUserId());
                break;
            // Apotheke
            case 4:
                this.$router.push("/patients");
                break;
        }
    },
}
</script>